<script>
	import { onMount } from 'svelte';
	import moment from 'moment';
	import messages from './store.js';

	const scrollDown = _ => {
		chatWindow.scrollTop = chatWindow.scrollHeight;
		chatInput.value = '';
	};

	const sendChat = async _ => {
		if(chatInput.value.trim() === '') return;
		const message = { senderId: user.id, content: chatInput.value, time: new Date() };
		const res = await fetch('https://svelte-chat-73f96.firebaseio.com/chats.json', {
			method: 'POST',
			body: JSON.stringify({message, user}),
			headers: { 'Content-Type': 'application/json' }
		});
		res.json()
			.then(res => {
				chats = [...chats, {message, user}];
			})
			.then(scrollDown);
	}
	
	let users = [
		{ id: 1, username: 'surfingtonio', firstname: 'Marc', lastname: 'Danting' },
		{ id: 2, username: 'jsmith', firstname: 'Jane', lastname: 'Smith' }
	];
	let user = users[0];
	let chats = [];
	let chatWindow;
	let chatInput;
	
	onMount(async () => {
		const res = await fetch('https://svelte-chat-73f96.firebaseio.com/chats.json');
		res.json()
			.then(data => {
				if(typeof(data) === undefined || data === null) 
					return [];

				chats = Object.values(data);
			})
			.then(scrollDown);
	});
</script>

<main>
	<div class="chat-items" bind:this={chatWindow}>
		{#each chats as chat}
			<div class={chat.user.id === user.id ? 'chat-item you' : 'chat-item'}>
				<div class="chat">
					<div class="user">
						<span class="username">{chat.user.username}</span>
					</div>
					<div class="message">
						<span class="content">{chat.message.content}</span>
						<span class="time">{moment(chat.message.time).format('h:mm:ss a')}</span>
					</div>
				</div>
			</div>
		{/each}
	</div>
	<form class="chat-controls">
		<input type="text" class="chat-input" placeholder="Type a message..." bind:this={chatInput} />
		<button type="submit" on:click|preventDefault={sendChat}>Send</button>
	</form>
</main>

