		--min-width: 480px;
		--message-color: #3F4C5C;
		--message-color-user: #163B60;
		--scrollbar-thumb-color: rgb(58, 67, 77);
		--scrollbar-thumb-hover-color: rgb(92, 99, 108);
	}

	main {
		align-items: stretch;
		background-color: var(--primary-bg-color);
		color:var(--primary-color);
		height: 100%;
	}

	.chat-items {
		height: calc(100vh - 3rem - 1px);
		overflow-y: scroll;
		overflow-x: hidden;
		min-width: var(--min-width);
		max-width: var(--max-width);;
		margin: 0 auto;
	}

	::-webkit-scrollbar {
		width: .5rem;
	}

	::-webkit-scrollbar-track {
		background-color: transparent;
	}

	::-webkit-scrollbar-thumb {
		background: var(--scrollbar-thumb-color);
		border-radius: .2rem;
		opacity: .1;
	}

	::-webkit-scrollbar-thumb:hover {
		background:var(--scrollbar-thumb-hover-color);
		opacity: 1;
	}

	.chat-item {
		display: flex;
		margin: .5rem 0;
		padding: 0 1rem;
		width: calc(100% - 2rem);
	}

	.user {
		margin-bottom: .25rem;
	}

	.you .user {
		text-align: right;
	}

	.user .username {
		color: var(--secondary-color);
		font-size: smaller;
	}

	.message {
		background-color: var(--message-color);
		border-radius: 0 1rem 1rem 1rem;
		justify-content: space-between;
		max-width: 30rem;
		padding: .5rem 1rem;
	}

	.you {
		justify-content: flex-end;
	}

	.you .message {
		background-color: var(--message-color-user);
		border-radius: 1rem 0 1rem 1rem;
	}

	.message .time {
		color: var(--secondary-color);
